*,
*::before,
*::after {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: Gilroy, sans-serif;
}

a {
  color: inherit;
  text-decoration: none;
}

ul,
li {
  list-style: none;
  padding: 0;
  font-size: 16px;
}

img {
  vertical-align: middle;
  border-style: none;
}

.container {
  max-width: 1200px;
  width: 100%;
  margin: 0 auto;
  padding: 0px 16px;
  position: relative;
}

body {
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center right;
  background-origin: padding-box;
  min-height: 100vh;
  font-size: 16px;
}

.header {
  background-color: white;
  color: black;
  position: sticky;
  width: 100%;
  top: 0;
  z-index: 99;
}
.header .nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 8px 0px;
}
.header .nav-content {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
}
.header .nav-right {
  display: flex;
  align-items: center;
}
.header .nav .logo img {
  max-width: 55px;
}
.header .nav .logo-text {
  margin-left: 20px;
}
.header .nav .logo-text span {
  font-size: 16px;
  font-weight: 500;
}
.header_content {
  display: none;
  padding: 16px 6px;
  background-color: #fff;
  color: black;
  border-top: 1px solid rgba(211, 211, 211, 0.9176470588);
  transition: display 0.8s ease-in-out;
}
.header_content.active {
  display: block;
  transition: display 0.8s ease-in-out;
  overflow-y: scroll;
  height: calc(100vh - 70px);
}
.header.active {
  box-shadow: 2px 4px 25px rgba(0, 0, 0, 0.4);
}

.hidden {
  overflow: hidden;
}

.content_menu {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(240px, 1fr));
  gap: 25px;
}
.content_menu .tmenu {
  margin: 6px 0px;
  padding-left: 3px;
}
.content_menu .tmenu li {
  word-wrap: break-word;
  margin-bottom: 4px;
}
.content_menu .tmenu li a {
  font-size: 16px;
  line-height: 1.5;
}
.content_menu .tmenu li a:hover {
  text-decoration: underline;
}
.content_menu .address-title {
  margin: 6px 0px;
  display: block;
  font-size: 14px;
}
.content_menu .menu-title {
  font-size: 18px;
  font-weight: 700;
}
.content_menu .letter p {
  font-size: 14px;
}
.content_menu .letter form {
  width: 100%;
  margin-top: 10px;
  position: relative;
}
.content_menu .letter form input {
  border: none;
  border-bottom: 1px solid rgba(211, 211, 211, 0.9176470588);
  outline: none;
  width: 100%;
  height: 25px;
  padding-left: 4px;
}
.content_menu .letter form .letter_btn {
  border: none;
  outline: none;
  background-color: transparent;
  cursor: pointer;
  width: 10px;
  height: 10px;
  background-image: url("../img/icons/right.png");
  background-repeat: no-repeat;
  background-size: contain;
  background-position: 0 0;
  position: absolute;
  right: 0;
  transform: translateY(50%);
}
.content__soc {
  display: flex;
  flex-direction: column;
  justify-content: start;
}
.content__soc a {
  display: flex;
  align-items: center;
  margin: 4px 0px;
}
.content__soc a b {
  padding-left: 4px;
}
.content__soc a:hover {
  text-decoration: underline;
}

.hamburger {
  position: relative;
  width: 30px;
  height: 18px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  cursor: pointer;
  transition: all ease 0.6s;
}
.hamburger__item {
  display: block;
  width: 30px;
  height: 2px;
  background-color: black;
  transition: all 0.3s ease-in-out;
}
.hamburger.active {
  transition: transform 0.4s ease;
}
.hamburger.active .hamburger__item:nth-child(2) {
  opacity: 0;
}
.hamburger.active .hamburger__item:nth-child(1) {
  transform: translateY(8px) rotate(45deg);
}
.hamburger.active .hamburger__item:nth-child(3) {
  transform: translateY(-8px) rotate(-45deg);
}

footer {
  padding: 24px 0;
  border-top: 1px solid rgba(211, 211, 211, 0.9176470588);
}
footer .footer_bottom {
  padding: 6px 0px;
  border-top: 1px solid rgba(211, 211, 211, 0.9176470588);
  margin-top: 4px;
  display: flex;
  align-items: center;
  justify-content: space-around;
}

.category-title {
  font-size: 32px;
  font-weight: bold;
  position: relative;
  line-height: 1.5;
}

.news {
  padding: 60px 0;
}

.ekspo {
  padding: 60px 0;
  display: flex;
  align-items: center;
}

.news-content {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(48%, 2fr));
  gap: 20px;
  padding: 12px 0px;
}
.news-content__item {
  position: relative;
}
.news-content__item__text {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  flex-direction: column;
  color: white;
  text-align: center;
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  top: 0;
  padding: 8px;
  opacity: 0;
  transition: opacity 0.5s ease-in-out;
}
.news-content__item__text p {
  position: relative;
}
.news-content__item__text a {
  position: relative;
}
.news-content__item__text a::after {
  content: "";
  position: absolute;
  right: -17px;
}
.news-content__item__text a:hover {
  text-decoration: underline;
}
.news-content__item__text:hover {
  opacity: 1;
}
.news-content__item__text::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.7);
}
.news-content-img {
  max-width: 100%;
  max-height: 100%;
  object-fit: cover;
  width: 100%;
  height: 100%;
}
.news-content-left {
  font-size: 18px;
}

.news-item {
  position: relative;
  padding-left: 15px;
  line-height: 1.5;
  margin: 6px 0px;
  max-width: 70%;
  cursor: pointer;
}
.news-item.active {
  font-weight: 600;
}
.news-item::before {
  content: "";
  width: 10px;
  height: 2px;
  position: absolute;
  left: 0;
  top: 50%;
  background-color: #000;
}

.news-title {
  font-size: 24px;
  font-weight: bold;
  margin-bottom: 0.5rem;
}
.news-title a:hover {
  text-decoration: underline;
}

.news-item-content {
  display: none;
}
.news-item-content.active {
  display: block;
}

.ticket {
  display: none;
  margin-right: 30px;
  width: 107px;
}
.ticket.active {
  display: block;
}
.ticket a {
  display: flex;
  align-items: center;
}
.ticket a:hover {
  text-decoration: underline;
}
.ticket a img {
  padding-left: 4px;
}

.news-content__item {
  display: none;
}
.news-content__item.active {
  display: block;
}

.feature {
  padding: 60px 0;
  display: flex;
  align-items: center;
}
.feature_content {
  padding: 32px 0px;
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(48%, 1fr));
  gap: 20px;
}
.feature__item {
  display: flex;
  flex-direction: column;
}
.feature__item__img {
  padding-bottom: 8px;
}
.feature__item__img img {
  max-width: 50px;
}
.feature__item a {
  font-size: 18px;
}
.feature__item a:hover {
  text-decoration: underline;
}
.feature__item .link-next {
  display: flex;
  align-items: center;
  justify-content: start;
  height: 100%;
}
.feature__item .link-next a {
  position: relative;
}
.feature__item .link-next a::after {
  content: ">";
  position: absolute;
  top: 2px;
  right: 0;
  left: 100%;
  width: 12px;
  height: auto;
  font-weight: normal;
}

.basic {
  padding: 60px 0;
  display: flex;
  align-items: center;
}
.basic_content {
  padding: 12px 0px;
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(45%, 1fr));
  gap: 20px;
}

.bn-title {
  font-size: 30px;
  font-weight: bold;
}

.btn-top {
  display: none;
  position: fixed;
  bottom: 20px;
  right: 30px;
  z-index: 100;
  background-color: #fff;
  box-shadow: 2px 4px 25px rgba(0, 0, 0, 0.4);
  color: black;
  cursor: pointer;
  border-radius: 8px;
  font-size: 24px;
  font-weight: bold;
  width: 40px;
  height: 40px;
}
.btn-top div {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: flex-start;
  justify-content: center;
  padding-top: 5px;
}
.btn-top:hover {
  opacity: 0.9;
}

.news-view {
  display: grid;
  grid-template-columns: 60% 39%;
  gap: 20px;
  text-align: justify;
  padding-bottom: 20px;
}
.news-view-img {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: end;
}
.news-view-img img {
  max-width: 380px;
  max-height: 100%;
  margin: 16px 0px;
}

.n-title {
  font-size: 26px;
  font-weight: bold;
}

.n-text {
  font-size: 16px;
  font-weight: normal;
  padding: 6px 0px;
  line-height: 1.5;
}

.bg-change {
  width: 100%;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
  display: block;
  position: relative;
  transition: all 0.8s ease;
}
.bg-change::before {
  content: "";
  background-color: rgba(0, 0, 0, 0.6509803922);
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
}

.head-content {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100%;
  padding: 24px 0px;
  height: 700px;
  color: white;
  padding-top: 60px;
}
.head-content div:first-child {
  max-width: 50%;
}
.head-content .ekspo-info {
  display: flex;
  flex-direction: column;
}
.head-content .ekspo-info__item {
  margin: 10px 0px;
  position: relative;
  padding-bottom: 5px;
}
.head-content .ekspo-info__item::after {
  content: "";
  width: 140px;
  height: 1px;
  background-color: rgba(211, 211, 211, 0.9176470588);
  position: absolute;
  bottom: 0;
}
.head-content .ekspo-info a:hover {
  text-decoration: underline;
}

.other {
  padding: 30px 0px;
  border-top: 1px solid rgba(211, 211, 211, 0.9176470588);
}
.other-content {
  display: grid;
  grid-template-columns: 48% 48%;
  gap: 20px;
  padding: 20px 0px;
}
.other-content a:hover {
  text-decoration: underline;
}

.gallery {
  padding: 30px 0;
}
.gallery-photos {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(20%, 1fr));
  grid-gap: 15px;
  padding: 15px;
}
.gallery-photos__item {
  position: relative;
}
.gallery-photos .text-gallery {
  position: absolute;
  left: 0;
  bottom: 0;
  right: 0;
  background: rgba(0, 0, 0, 0.493);
  color: white;
  padding: 3px;
  opacity: 0;
  transition: opacity 0.5s ease;
}
.gallery-photos a img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  display: block;
}
.gallery-photos a:hover .text-gallery {
  opacity: 1;
}

.img-fluid {
  max-width: 100%;
  height: auto;
}

.section__block {
  padding: 60px 6px;
}
.section__block .category-title {
  text-align: center;
}
.section__block .category-title::before, .section__block .category-title::after {
  display: none;
}

.structures {
  position: relative;
}
.structures__item {
  position: relative;
  padding: 10px 0px;
}
.structures__item .st-title::before {
  content: "";
  display: block;
  width: 12px;
  height: 2px;
  background-color: #000;
  transform: translate(-15px, 12px);
}
.structures__item::before {
  content: "";
  position: absolute;
  width: 2px;
  height: 100%;
  background-color: #000;
  transform: translate(-15px, 12px);
}
.structures__item__link {
  display: flex;
  flex-wrap: wrap;
  align-items: start;
}
.structures__item__link a {
  padding: 8px;
  width: 25%;
}
.structures__item__link a:hover {
  text-decoration: underline;
}
.structures__item:last-child::before {
  display: none;
}

.st-title {
  font-size: 22px;
  font-weight: bold;
}
.st-title a:hover {
  text-decoration: underline;
}

.link {
  color: #464242;
}
.link::after {
  content: "";
  padding-left: 1px;
  font-size: 14px;
}

.linkhref:hover {
  text-decoration: underline;
}

.info__block {
  display: grid;
  grid-template-columns: 0.4fr 1fr;
  grid-gap: 20px;
  margin: 12px 0px;
}
.info__block .link {
  display: inline-block;
  margin-top: 12px;
}
.info__block .link:hover {
  text-decoration: underline;
}
.info__block.gap {
  grid-gap: 60px;
}
.info__block .img-mob {
  display: none;
}

.my-1 {
  margin: 1em 0em;
}

.payment__form {
  color: #464242;
  display: flex;
  flex-direction: column;
}
.payment__form label {
  font-size: 16px;
  font-weight: 600;
}
.payment__form input,
.payment__form textarea {
  width: 360px;
  height: 30px;
  border: none;
  border-bottom: 1px solid #a8a8a8;
  color: #a8a8a8;
  outline: none;
  padding: 3px;
}
.payment__form select {
  width: 360px;
  height: 30px;
  color: #a8a8a8;
  outline: none;
  margin: 3px 0px;
  padding: 3px;
}
.payment__form__item {
  display: flex;
  flex-direction: column;
  margin: 6px 0px;
}
.payment__form .btn-submit {
  background-color: transparent;
  color: #464242;
  font-size: 17px;
  font-weight: 600;
  display: flex;
  align-items: center;
  justify-content: space-between;
  border: none;
  width: 100px;
  cursor: pointer;
}

.links {
  display: flex;
  flex-direction: column;
  padding-left: 6px;
}
.links a {
  margin: 3px 0px;
  width: fit-content;
}

.contact__link p {
  margin: 4px 0;
  font-weight: 600;
  font-size: 18px;
}
.contact__link p a:hover {
  text-decoration: underline;
}

.md-block {
  max-width: 50%;
}

.block__grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(40%, 1fr));
  gap: 50px;
  margin: 12px 0px;
}
.block__grid__wrapper {
  max-width: 457px;
}
.block__grid__item {
  display: flex;
  flex-direction: column;
  max-width: 100%;
  width: 100%;
  margin-bottom: 16px;
  align-self: center;
  position: relative;
}
.block__grid__item .img-cover {
  max-width: 100%;
  max-height: 260px;
  overflow: hidden;
}
.block__grid__item .img-cover img {
  object-fit: cover;
  width: 100%;
}
.block__grid__item a {
  margin: 4px 0px;
  font-weight: 600;
  display: block;
}
.block__grid__item a:hover {
  text-decoration: underline;
}

.desc-monument {
  display: inline-block;
  position: relative;
}
.desc-monument p {
  text-align: left;
  padding-right: 4px;
  font-weight: 500;
  font-size: 18px;
}

.mini-title {
  font-size: 20px;
  font-weight: 600;
}

.by-ticket_btn {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
}

.res-contact {
  display: flex;
  align-items: center;
  width: 200px;
}
.res-contact input {
  width: 30px;
  height: 30px;
}
.res-contact label {
  padding: 4px;
  font-size: 12px;
}

.resmail {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
}
.resmail button {
  padding: 8px 12px;
  border: 0;
  outline: 0;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  font-size: 16px;
  background: none;
  font-weight: 600;
  cursor: pointer;
}
.resmail button img {
  margin-left: 6px;
}

.lang.active {
  display: block;
}

.swiper {
  cursor: grab;
}

.swiper-head {
  width: 100%;
  height: 800px;
}
.swiper-head .swiper-slide {
